<!-- 文件往来页面 -->
<template>
	<view>
		<u-tabs :list="tabList" lineWidth="30" @click="clickTab" :activeStyle="{
		        color: 'rgb(78, 130, 255)',
		        fontWeight: 'bold',
		        transform: 'scale(1.05)'
		    }" :inactiveStyle="{
		        color: '#606266',
		        transform: 'scale(1)'
		    }">
		</u-tabs>

		<view v-if="currentTab === '文件查看'">
			<view class="search-input">
				<u--input placeholder="输入文件名搜索" prefixIcon="search" prefixIconStyle="font-size: 50rpx;color: #909399"
					v-model="query.fileName" @input="$u.debounce(searchList, 500)"></u--input>
			</view>
			<view v-if="files.length || status === 'loading'" style="margin-top: 84rpx;">
				<u-list @scrolltolower="scrolltolower" height="calc(100vh - 126rpx)">
					<u-list-item v-for="(item, index) in files" :key="index">
						<u-cell center @click="click(item)">
							<u-icon slot="icon" name="file-text-fill" color="#2979ff" size="45"></u-icon>
							<view slot="title" class="u-slot-title">
								<text class="name u-line-1" style="font-size: 35rpx;">{{ item.fileName }}</text>
								<view class="flexb" style="margin-top: 16rpx;">
									<u--text type="info" :text="item.createTime"></u--text>
								</view>
							</view>
						</u-cell>
					</u-list-item>
					<u-loadmore :status="status" />
				</u-list>
			</view>
			<u-empty v-else mode="list" marginTop="40%" icon="http://cdn.uviewui.com/uview/empty/list.png" />
			<u-popup :show="showVideo" @close="showVideo = false" mode="center">
				<video style="width: 100vw;" v-if="showVideo" :src="showVideoUrl" @error="fail"></video>
			</u-popup>

		</view>
		<MessageBoard v-else />
	</view>
</template>

<script>
	// 留言板页面组件
	import MessageBoard from './messageBoard.vue'

	const {
		id,
		userType
	} = uni.getStorageSync('user_info');
	export default {
		components: {
			MessageBoard
		},
		data() {
			return {
				tabList: [{
					name: '文件查看',
				}, {
					name: '留言板',
				}],
				// 当前tab
				currentTab: '文件查看',
				// 文件列表数据
				files: [],
				query: {
					page: 1,
					rows: 30,
					fileName: '',
				},
				status: 'loading',
				// 打开文件状态
				openFileLoading: false,
				// 是否显示播放器
				showVideo: false,
				// 播放地址
				showVideoUrl: ''
			};
		},
		onLoad() {
			this.loadmore()
		},
		methods: {
			searchList() {
				this.query.page = 1
				this.files = []
				this.loadmore()
			},
			scrolltolower() {
				this.loadmore()
			},
			async loadmore() {
				this.status = 'loading'
				const {
					rows,
					records
				} = await uni.$u.http.post(`/downloadCenter/selectListByPage`, null, {
					params: this.query,
					custom: {
						loading: false
					}
				})
				if (!rows) return
				if (rows.length === 0 || rows.length < this.query.rows) {
					if (records > this.files.length) this.files.push(...rows)
					return this.status = 'nomore'
				}
				if (rows.length >= this.query.rows) {
					this.files.push(...rows)
					this.query.page++
					this.status = 'loadmore'
				}
			},
			// 预览文档
			previewFile(fileUrl) {
				uni.downloadFile({
					url: fileUrl,
					success: function(res) {
						var filePath = res.tempFilePath;
						uni.openDocument({
							filePath: filePath,
							showMenu: true,
						});
					},
					fail: this.fail
				});
			},
			// 预览图片
			previewImg(fileUrl) {
				uni.previewImage({
					urls: [fileUrl],
					fail: this.fail
				});
			},
			// 预览视频
			previewVideo(fileUrl) {
				this.showVideo = true
				this.showVideoUrl = fileUrl
			},
			click({
				fileUrl
			}) {
				const arr = fileUrl.split('.')
				const type = arr[arr.length - 1].toLocaleLowerCase()
				const imgTypes = [
					"bmp",
					"jpg",
					"png",
					"tif",
					"gif",
					"pcx",
					"tga",
					"exif",
					"fpx",
					"svg",
					"psd",
					"cdr",
					"pcd",
					"dxf",
					"ufo",
					"eps",
					"ai",
					"raw",
					"WMF",
					"webp",
					"avif",
					"apng"
				]

				const fileTypes = [
					"doc",
					"xls",
					"ppt",
					"pdf",
					"docx",
					"xlsx",
					"pptx"
				]

				const videoTypes = [
					"avi",
					"wmv",
					"mpeg",
					"mp4",
					"m4v",
					"mov",
					"asf",
					"flv",
					"f4v",
					"rmvb",
					"rm",
					"3gp",
					"vob"
				]
				if (fileTypes.includes(type)) return this.previewFile(fileUrl)
				if (imgTypes.includes(type)) return this.previewImg(fileUrl)
				if (videoTypes.includes(type)) return this.previewVideo(fileUrl)
				uni.showToast({
					title: '暂不支持该文件类型',
					type: 'error'
				})


				switch (item.fileUrl) {
					case 'value':
						break;
					default:
						break;
				}

				this.previewImg()
				this.previewVideo()

				uni.showLoading({
					title: '正在打开文件'
				})

			},
			clickTab(data) {
				this.currentTab = data.name
			},
			fail(e) {
				uni.hideLoading()
				uni.showToast({
					title: e.errMsg,
					icon: 'none'
				})
			}
		}
	}
</script>

<style lang="scss">
	.search-input {
		box-sizing: border-box;
		// box-shadow: 0px 0px 60rpx #e2e2e2;
		background: transparent;
		position: absolute;
		top: 100rpx;
		width: 96vw;
		left: 2vw;

	}

	.u-tabs__wrapper__nav {
		justify-content: center;
	}

	.u-tabs__wrapper__nav__line {
		margin-right: 200rpx;
	}
</style>